.footer {
    color: white;
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    padding-left: 8%;
    background-color: #222;
    box-shadow: 0 -14px 28px rgba(0, 0, 0, 0.25),
        0 -10px 10px rgba(0, 0, 0, 0.22);
}

.item {
    padding-left: 3%;
    color: white;
    text-decoration: none;
}

.active {
    color: rgb(255, 236, 64);
    text-decoration: none;
}

.footer_item {
    width: 100%;
    position: absolute;
    right: 0;
}